@import "~materialize-css/dist/css/materialize.min.css";

body {
    min-width: 320px;
    margin: 0;
    padding: 0;
    font-family: "Montserrat", Arial, sans-serif;
    font-size: 19px;
    line-height: 23px;
    font-weight: 500;
    color: #000000;
    text-transform: none;
    background-color: transparent;
}

/*Шапка*/

.logo__container {
    padding: 0;
    margin: 0;

    text-align: center;

    position: relative;

    min-height: 35vh;
}

.logo__image {
    position: absolute;

    margin: 0;
    padding: 0;

    width: 250px;
    height: 150px;

    position: absolute;
    top: 5vh;
    left: 12%;
}

.background__image {
    position: absolute;

    top: 0vh;
    left: -8vw;

    z-index: -1;

    opacity: 0.65;

    width: 120%;
}
/*Tabs*/
.categories__button {
    color: #0d47a1 !important;
    font-size: 16px !important;
}

.tabs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 90px !important;
    background-color: transparent !important;
}

.categories .row .col .tab {
    margin: auto;
}
.categories {
  padding-top: 20px;
}

/*Карта блюда*/
.card-image {
    overflow: hidden;
}

.card {
    height: 450px!important;
}

.card-image-container {
    overflow: hidden;
    height: 280px;
}

.card-image {
    overflow: visible;
}
.card-content {
    padding-top: 34px !important;
}

.card .card-content .dish-description {
    height: 100px;
    margin-bottom: 5px;
    overflow: auto;
}

.card .card-image .card-title {
    color: #000;
    font-weight: 500;
    background-color: #fff;
    padding: 15px 15px 40px 15px;
}

/*Кнопки Заказа*/
.waiter_icon {
    position: absolute !important;

    font-size: 40px !important;

    top: -5% !important;
    left: 3vw !important;
}
.waiter-button {
    position: relative !important;
}

.waves-light {
    background-color: #2196f3!important;
}

/*Заказ*/
.collection-item {
    position: relative !important;
    padding-left: 80px !important;
    color: #0d47a1 !important;
}
.badge {
    position: absolute !important;
    width: 30px !important;
    height: 30px !important;
    background-color: transparent !important;
    border: none !important;
    top: 1vh !important;
    left: 4vw !important;
    padding: 0 !important;
    margin: 0 !important;
}

.order-list {
    margin: .5rem 0 2rem 0 !important;
}

.close_icon {
    position: absolute;
    font-size: 36px !important;
    top: -20% !important;
    left: -1vw !important;
}

/*Кнопка назад*/
.back_icon {
    color: #fff !important;
    position: absolute !important;
    left: 1vw !important;
    font-size: 40px !important;
}

.back-button {
    padding-left: 45px;

    position: relative;

    margin-bottom: 20px;
}

@media (max-width: 500px) {
    .categories .row .col .tab {
        width: auto;
    }
}

@media (min-width: 768px) {
    .container {
        width: 768px;
        margin: 0 auto;
    }
    .categories {
        padding: 20px;
    }
    .row:after {
        content: '';

        display: auto !important;
    }
    .container--menu {
        padding: 20px;
    }
    .logo__image {
        top: 3vh;
        left: 40%;
    }
    .background__image {
        object-fit: cover;
        margin: -3% 0 0 0;
        height: 100%;
    }
    .logo__container {
        min-height: 20vh;
    }
    .categories__button {
        font-size: 18px !important;
    }

}
@media (min-width: 1200px) {
    .container {
        width: 1200px;
        margin: 0 auto;
    }
    .row:after {
        display: auto !important;
    }
    .collection-item {
        position: relative !important;
        padding-left: 80px !important;
    }
    .logo__image {
        top: 3vh;
        left: 40%;
    }
    .background__image {
        object-fit: cover;
        margin: -3% 0 0 0;
        height: 100%;
    }
    .logo__container {
        min-height: 38vh;
    }
}
